import PropTypes from "prop-types"
import React from "react"
import ParallaxCard from 'react-parallax-card'
import githublogo from "../images/github.svg"

const Projectcard = ({ title, description, img, skills, link }) => (

  <div
    style={{
      display: "flex",
      borderRadius: 10,
      padding: "1rem",
      margin: "1rem",
      width: "100%",
      alignItems: 'center',
      justifyContent: "space-between",
      border: "1px solid #808080"
    }}
  >
    <div style={{ display: "flex", flexDirection: "column", }}>
      <h4 style={{ marginTop: "1rem", marginBottom: "1rem", justifyContent: 'center', }}>
        {title}
      </h4>
      <div>
        {skills.map((skill, index) => {
          let url = "https://img.shields.io/badge/" + skill.skill + "-%20-" + skill.color
          return <img
            src={url}
            alt={skill.type}
            key={index}
            style={{ marginRight: "0.5rem" }}
          />
        })}
      </div>
      <p>{description}</p>
      {link ? <a href={link} style={{ textDecoration: "none", color: "#333" }}>
        <div style={{ border: "1px solid grey", borderRadius: 5, maxWidth: "2.5rem", maxHeight: "2.5rem", padding: 10 }}>
          <img src={githublogo} alt="github logo">
          </img>
        </div>
      </a> : null}
    </div>
    <img src={img} alt={title} style={{ height: "14rem" }}></img>

  </div>

)

Projectcard.propTypes = {
  siteTitle: PropTypes.string,
}

Projectcard.defaultProps = {
  siteTitle: ``,
}

export default Projectcard
